import React from "react";

export default function Weather(i) {
  const { data } = i;

  if (!data) {
    return null;
  }

  if (!data.main) {
    return null;
  }

  const weatherIcon = `http://openweathermap.org/img/wn/${data.weather[0].icon}.png`;

  return (
    <div>
      <div>
        <img src={weatherIcon} alt="weather icon" className={data.icon} />

        <h1>{Math.round(data.main.temp)}°F</h1>
        <h3>{data.name}</h3>
        <p>
          <i>{data.weather[0].description}</i>
        </p>
      </div>

      <div className="dataInfo">
        <h3>{data.main.pressure} hPa</h3>
        <p>Pressure</p>

        <h3>{data.main.humidity}%</h3>
        <p>Humidity</p>

        <h3>{Math.round(data.wind.speed)} mph</h3>
        <p>Wind Speed</p>

        <h3>{data.wind.deg}°</h3>
        <p>Wind Direction</p>
      </div>
      <div>
        <p>
          <i>Reporting Time: {new Date(data.dt * 1000).toLocaleTimeString()}</i>
        </p>
      </div>
    </div>
  );
}
