import React, { useState } from "react";
import axios from "axios";

import "./styles.css";
import Search from "./Search";
import Weather from "./Weather";

const api = "aa6b85362237e088c2909a1d9d3faa5c";

export default function App() {
  const [data, setData] = useState({});

  const searchLocation = (latitude, longitude) => {
    const url = `https://api.openweathermap.org/data/2.5/weather?lat=${latitude}&lon=${longitude}&appid=${api}&units=imperial`;

    axios
      .get(url)
      .then((response) => {
        setData(response.data);
      })
      .catch((error) => {
        console.error(error);
        alert("Request failed with status code 400.");
      });
  };

  return (
    <div className="app">
      <h2>CIT382 - Project 6</h2>
      <div className="search">
        <Search onSearch={searchLocation} />
      </div>
      <div>
        <div className="data">
          <Weather data={data} />
        </div>
      </div>
    </div>
  );
}
