import { useState, useEffect } from "react";

export default function Search(props) {
  const { data } = props;

  const [latitude, setLatitude] = useState("");
  const [longitude, setLongitude] = useState("");
  const [selectedOption, setSelectedOption] = useState("current-location");

  const [presets, setPresets] = useState([
    { name: "Seattle", latitude: 47.6062, longitude: -122.3321 },
    { name: "Portland", latitude: 45.5152, longitude: -122.6784 },
    { name: "San Fransisco", latitude: 37.7749, longitude: -122.4194 },
    { name: "Los Angeles", latitude: 34.0522, longitude: -118.2437 },
    { name: "San Diego", latitude: 32.7157, longitude: -117.1611 }
  ]);

  const handleOptionChange = (e) => {
    setSelectedOption(e.target.value);
    const presetLocation = presets.find(
      (location) => location.name === e.target.value
    );
    if (presetLocation) {
      setLatitude(presetLocation.latitude);
      setLongitude(presetLocation.longitude);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!latitude || !longitude) {
      alert("Request failed. Enter valid latitude and longitude.");
      return;
    }

    props.onSearch(latitude, longitude);
  };

  const handleSavePreset = () => {
    if (!latitude || !longitude) {
      alert("Please enter a valid location before saving preset.");
      return;
    }

    const cityName = data ? data.name : `Preset ${presets.length + 1}`;
    const newPreset = {
      name: cityName,
      latitude,
      longitude
    };
    setPresets([...presets, newPreset]);
    setSelectedOption(newPreset.name);
  };

  useEffect(() => {
    if (navigator.geolocation && selectedOption === "current-location") {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLatitude(position.coords.latitude);
          setLongitude(position.coords.longitude);
        },
        (e) => {
          console.error(e);
          if (e.code === e.PERMISSION_DENIED) {
            alert("Location denied. Please enter your location manually.");
          }
        }
      );
    }
  }, [selectedOption]);

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <div>
          <label>
            Latitude:
            <input
              type="text"
              value={latitude}
              onChange={(e) => setLatitude(e.target.value)}
            />
          </label>
          <br />
          <label>
            Longitude:
            <input
              type="text"
              value={longitude}
              onChange={(e) => setLongitude(e.target.value)}
            />
          </label>
        </div>
        <div>
          <label>
            Select a preset location:
            <select value={selectedOption} onChange={handleOptionChange}>
              <option value="currentLocation">Current Location</option>
              {presets.map((preset) => (
                <option key={preset.name} value={preset.name}>
                  {preset.name}
                </option>
              ))}
            </select>
          </label>
        </div>
        <div>
          <button type="submit">Search</button>
          <button onClick={handleSavePreset}>Save Preset</button>
        </div>
      </form>
    </div>
  );
}
